.header{

color: white;
display: flex;
padding: 20px;
align-items: center;
justify-content: space-between;
overflow-x: scroll;


}

.header::-webkit-scrollbar{
  display: none;
}

.header{

-ms-overflow-style: none;
scrollbar-width: none;


}






.header_icon > .MuiSvgIcon-root{
margin-left: auto;
margin-right: auto;
font-size: 30px;

}

.header_icon--active > p{

display: inline-flex !important;

}




.header_icon:hover > p{

  display: inline-flex;
}



.header_icon > p{
display: none;
text-transform: uppercase;
letter-spacing: 3px;
margin-left: auto;
margin-right: auto;
font-size: small;
}

.header_icon{

display: flex;
flex-direction: column;
width: 100px;
cursor: pointer;




}


.header_icons{

display: flex;
align-items: center;
text-align: center;



}

.header > img {

  object-fit: contain;
  height: 80px;
}

.lo{
  color:rgb(255, 255, 255);
  margin-right: 50px;
  font-size: 50px;
}