.Video{
color: white;
width: 400px;
height: 400px;
padding: 15px;
transition: transform 100ms;


}


.Video:hover{

transform: scale(1.09);

}

.Video > img{

height: 200px;
object-fit: contain;
width: 400px;

}

.Video > h2{

  padding-top: 5px;
  font-size: 15px;
  text-align: center;
}

.Video span{

font-size: 10px;
text-align: center;
display: block;
word-wrap: normal;

}


.video_stats{
  display: none;
}
.Video:hover >.video_stats{

  display: inline;
 
}




